@font-face{
  font-family: 'Geist';
  src: url('./fonts/Geist.ttf');
}
@font-face{
  font-family: 'Geist Mono';
  src: url('./fonts/GeistMono.ttf');
}

:root{
  --whiter: #f3f3f6;
  --white: #e7e8ed;
  --dark-blue: #0e2ce2;
  --blue: #0D6DFF;
  --grey: #81869B;
  --light-grey: #c6c7cc;
  --dark-grey: #323237;
  --light-blue: #1FA3C1;
  --black: #101218;
  --grey-hover: rgb(225, 227, 230);
  --article-bg: #e7e8ed;
  --box-shadow: 0 1px 2px 0 rgba(0,0,0,.04);
  --filter-color:  invert(93%) sepia(5%) saturate(581%) hue-rotate(180deg) brightness(95%) contrast(95%);

}
html{
  font-size: clamp(8px, 0.71vw, 16px); /* 13.6px*/
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}
#root{
  width: 100%;
  display:flex;
  align-items: center;
  flex-direction: column;
}
*{
  margin: 0;
  padding: 0;
}
body {
  display: flex;
  align-items: center;
  flex-direction: column;
  overflow-x:hidden;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--white);
}
a{
  text-decoration: none;
}
h1, h2, h3, h4, h5, .default-header{
  font-family: 'Geist';
  font-weight: 600;
  color: var(--dark-blue);
  line-height: 112.5%;
}
h1.white{
  font-weight: 500;
}
h1, .h1{
  font-size: 3rem;
  letter-spacing: -0.09rem;
  font-weight: 600;
}
h1.alt, h2.alt, h3.alt, h4.alt{
  font-family: 'Geist Mono';
  font-weight: 700;
  text-transform: uppercase;
  color: var(--dark-blue);
}

h1.center, p.center{
  text-align: center;
}
h2, .h2{
  font-size: 2.5rem;
  font-weight: 600;
  letter-spacing: -0.075rem;
}
h3, .h3{
  font-size: 2rem;
  font-weight: 600;
  letter-spacing: -0.06rem;

}
h4{
  font-size: 1.4rem;
  font-weight: 600;
  letter-spacing: -0.042rem;
}
h4.white{
  font-weight: 500;
}
h5{
  font-size: 1rem;
  font-weight: 600;
  letter-spacing: -0.03rem;

}
p{
  font-size: 1rem;
  font-family: 'Geist';
  font-weight: 400;
  letter-spacing: -0.03rem;
  color: var(--grey);
}
.bold{
  font-weight: 700;
}

.letter{
  transform-origin: 0%;
  display: inline-block;
  line-height: 100%;
}
.letter-wrapper{
  position: relative;
  display: inline-block;
  overflow: hidden;
  line-height: 100%;
}
p > .letter-wrapper > .letter{
  font-size: 1.25rem;
  letter-spacing: -0.06rem;
  color: var(--grey);
  font-weight: 600;
  transform-origin: 0%;
  display: inline-block;
}

.label{
  font-family: 'Geist Mono';
  font-size: 0.85rem;
  font-weight: 400;
  text-transform: uppercase;
  margin: 0.5rem 0;
}
p.label.white{
  font-weight: 300;
}
.white, h1.alt.white, h2.alt.white{
  color: var(--white);
}
.grey{
  color: var(--dark-grey);
}
.blue{
  color: var(--blue);
}
.light-blue{
  color: var(--light-blue);
}
.card{
  border-radius: 0.15rem;
  border: 1px solid var(--light-grey);
  box-shadow: var(--box-shadow);
  width: 25rem;
  background-color: var(--white);
  opacity: 1;
  padding: 1rem 1.5rem;
  padding-bottom: 2rem;
}
.card:hover{
  opacity: 0.75;
}

.wrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  padding: 8rem clamp(20px, 16vw, 400px);
}
.container{
  position: relative;
  box-sizing: border-box;
  display: flex;
  width: 100%;
  max-width: 1920px;
  flex-wrap: wrap;
}
.container.blue{
  background-color: var(--blue);
}
.container.dark-blue{
  background-color: var(--dark-blue);
}
.background-dark{
  background-image: linear-gradient(30deg, var(--dark-blue), var(--blue));
}
.container.border, .container.col.border{
  border: 1px solid var(--light-grey);
}
.contain.der.border:nth-last-of-type(), .container.border:first-child{
  border-bottom-width: 1px solid var(--light-grey);
}
.wallet-container{
  background-color: var(--white);
  border-radius: 0.5rem;
  border: 1px solid var(--light-grey);
  box-shadow: var(--box-shadow);
  width: 29rem;
  box-sizing: border-box;
  overflow-x: hidden;
}
.wallet-pages{
  min-width: 25rem;
  flex-wrap: nowrap;
  transition: transform 0.25s ease-in;
}
.max-height{
  height: 100vh;
  max-height: 1080px;
}
.max-width{
  width: 100%;
}

.container.half-width{
  width: 50%;
}

.popup{
  position: fixed;
  top: 0;
  left: 0;
  justify-content: center;
  align-items: center;
  background: rgba(210, 214, 226, 0.9);
  z-index: 1002;


}
.wallet-container::-webkit-scrollbar { 
  display: none;  /* Safari and Chrome */
}
.wallet-container{
  -ms-overflow-style: none;  /* Internet Explorer 10+ */
  scrollbar-width: none;  /* Firefox */
}
.popup.show{
  opacity: 1;
  visibility: visible;
  transition: opacity 0.25s ease;


}
.popup.hide{
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.25s ease;
}
.popup.show > .wallet-container{
  transform: translateY(0rem);
  opacity: 1;
  transition: transform 0.25s ease, opacity 0.25s ease;
  
}

.popup.hide > .wallet-container{
  transform: translateY(1.5rem);
  opacity: 0;
  transition: transform 0.25s ease, opacity 0.25s ease;
}


[class*="col"]{
  display: flex;
  flex-direction: column;
}
.col > .button{
  margin-top: 0.5rem;
}
.col-1{
  flex: 1;
  
}
.col-2{
  flex: 2;
}
.col-3{
  flex: 3;
}
.col-4{
  flex: 4;
}
.col-5{
  flex: 5;
}
.col-7{
  flex: 7;
}

.width-2{
  width: calc(100%/3);
}
.width-3{
  width: calc(200%/3);
}

.col-1.padding, .col-2.padding{
  padding: 2rem 1.5rem;
}
.col.padding{
  padding: 5rem;
}
.col.border{
  border-right: 1px solid var(--light-grey);
}
.col.border:nth-child(3){
  border-right-width: 0px;
}
.col.border:last-child{
  border-left-width: 0px;
}
.border-top{
  border-top: 1px solid var(--light-grey);
}
.border-bottom{
  border-bottom: 1px solid var(--light-grey);
}
.border-right{
  border-right: 1px solid var(--light-grey);
}

.inline-block{
  display: inline-block;
}
.relative{
  position: relative;
}
.absolute{
  position: absolute;
}
.box-shadow{
  box-shadow: var(--box-shadow);
}

.flex{
  display: flex;
}
.flex-col{
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}
.flex-row{
  flex-direction: row;
}
.flex-end{
  justify-content: flex-end;
}
.align-self-end{
  align-self: flex-end;
  margin-left: auto;
}
.col.align-self-end{
  margin-left: auto;
}
.justify-flex-end{
  margin-top: auto;
}

.container.padding{
  padding: 6.5rem 4.5rem  ;
}
.container.padding-top, .padding-top{
  padding-top: 6.5rem;
}
.padding-light{
  padding: 2rem;
}
#landing.padding, .banner.padding, .footer.padding{
  /*padding: 8rem 15rem;*/
  padding: 8rem clamp(20px, 16vw, 400px);
}
.center{
  justify-content: center;
  align-items: center;
}
.align-center{
  align-items: center;
}
.justify-center{
  justify-content: center;
}
.margin{
  margin-top: 1rem;
}
.margin-2{
  margin-top: 2rem;
}
.margin-right{
  margin-right: 1rem;
}
.margin-left{
  margin-left: 1rem;
}
.margin-bottom{
  margin-bottom: 1rem;
}
#ibp-video{
  width: '100%'; 
  filter: 'saturate(0) brightness(1)'; 
  opacity: 0.25;
  transition: opacity 0.25s ease-in, filter 0.25s ease-in;
}
#ibp-video:hover{
  cursor:pointer;
  filter: 'saturate(1) brightness(1)';
  opacity: 0.5; 
}
.hide-overflow-y{
  overflow-y: hidden;
}
.hide-overflow-x{
  overflow-x: hidden;
}
.line{
  width: 100%;
  border-top: 1px solid var(--white);
  opacity: 0.5;
  margin: 0.5rem 0;
  transition: width 0.25s ease-in-out;
}
.line.dark{
  border-color: var(--grey);
}

.gradient{
  display: flex;
  position: absolute;
  height: 125px;
  width: 100%;
  left: 0;
  background: linear-gradient(#e7e8ed00, #e7e8ed, #e7e8ed);
  transform: translateY(-100%);
  transition: all 0.25s linear;
}

.button, button{
  position: relative;
  display: flex;
  flex-direction: column;
  flex-grow: 0;
  font-size: 1rem;
  font-weight: 600;
  font-family: 'Geist';
  padding: 0.75rem 1.5rem;
  box-sizing: border-box;
  margin-right: 1rem;
  justify-content: center;
  align-items: center;
  background-color: var(--white);
  border: 0.2rem solid var(--dark-blue);
  color: var(--dark-blue);
  height: fit-content;
  width: fit-content;
  overflow: hidden;
  cursor:pointer;
  transition: all 0.35s linear;
}
.button.disabled{
  pointer-events: none;
  opacity: 0.35;
  border-color: var(--light-grey);
  color: var(--grey);
}
.button:last-child{
  margin-right: 0;
}
.button:after, button:after{
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0%;
  background: var(--blue);
  transition: all 0.25s ease-in-out;
  color: var(--white);

}

.button.dark:after, button.dark{
  background: var(--white);
}
.button:hover::after, button:hover::after{
  top: 0;
  height: 100%;
  color: var(--white);
}
.button > span{
  z-index: 1;
  transition: all 0.25s ease-in-out;
}
.button.dark:hover > span{
  color: var(--dark-blue);
}
.button:hover > span{
  color: var(--white);
}
.icon-button{
  border: 1px solid;
  border-color: rgba(198, 199, 204, 0);
  border-radius: 200rem;
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.125s linear;
  padding: 0.25rem;
}
.icon-button.alt{
  border: 1px solid var(--light-grey);
}
.icon-button:hover{
  cursor: pointer;
  background-color: var(--dark-blue);
  border-color: var(--dark-blue);
}
.icon-button> img{
  transition: all 0.125s linear;
}
.icon-button:hover > img{
  transition: all 0.125s linear;
  filter: saturate(0%) brightness(255);
}
.chevron-down{
  width: 0.75rem;
  height:1rem;
}
.big.button{
  flex-direction: row;
  border-radius: 100rem;
  width: 100%;
  border-width: 1.4px;
  justify-content: flex-start;
  border-color: var(--light-grey);
  box-shadow: var(--box-shadow);
}
.big.button > span:first-child{
  font-size: 2rem;
  font-weight: 600;
  letter-spacing: -0.06rem;

}

.big.button > span:last-child{
  margin-left: auto;
}

.big.button > .icon{
  padding: 0.32rem;
  border: 1px solid;
  border-radius: 100rem;
  display: flex; 
  justify-content: center;
  align-items: center;
  height: 1.5rem;
  width: 1.5rem;
  padding: 0.5rem;
  background-color: var(--dark-blue);
  color: var(--white);
  border-color: var(--dark-blue);
}
.big.button:hover > .icon{
  border-color: var(--white);
  background-color: var(--white);
  color: var(--dark-blue);
}
.big.button > .icon > img{
  width: 100%;
  transition: 0.25s linear all;
  filter:

}
.big.button:hover > .icon > img{
  filter:invert(1);
  transition: 0.25s linear all;
}

.button.dark{
  background-color: var(--dark-blue);
  color: var(--white);
}
.options{
  padding: 1rem 1rem;
  border: 1px solid var(--light-grey);
  box-shadow: var(--box-shadow);
  transition: all 0.125s linear;
  align-items: center;
}
.options > span{
  font-family: 'Geist';
  font-weight: 600;
  letter-spacing: -3%;
  color: var(--dark-blue);
  transition: color 0.125s linear;

}

.options:hover{
  cursor: pointer;
  background-color: var(--grey-hover);
  color: var(--white);
}

.options > img{
  width: 1.8rem;
  height: auto;
  margin-left: auto;
}

.options.disabled{
  pointer-events: none;
  opacity: 0.35;
}
.options.unselected{
  opacity: 0.35;
}
.options.disabled > span, .options.disabled > img, .options.unselected > span, .options.unselected > img{
  filter: saturate(0%);
  opacity: 0.7;
}


.staking-option{
  min-width: 29rem;
  padding: 0rem 2rem 2rem 2rem;
}

.box-button{
  border: 1px solid var(--white);
  opacity: 0.5;
  height: 3rem;
 
}
.box-button > p{
  color: var(--white);
}

select{
  border: 1px solid var(--light-grey);
  padding: 0.5rem;
  border-radius: 0.25rem;
  font-family: 'Geist';
  letter-spacing: -3%;
  font-weight: 500;
  width: 100%;
  color: var(--dark-grey);
  box-shadow: var(--box-shadow);
}
.conviction-tick{
  font-family: 'Geist';
  letter-spacing: -3%;
}
.conviction-slider{
  background-color: var(--dark-blue);
}
.bond-amount{
  padding: 0.5rem;
  border: 1px solid var(--light-grey);
  box-shadow: var(--box-shadow);
  font-family: 'Geist Mono';
  font-size: 0.85rem;
  font-weight: 400;
  color: var(--dark-grey);
  background-color: var(--grey-hover);
}

.sticky-navbar > div:nth-of-type(2) > a > h5, .sticky-navbar > div:nth-of-type(2)  > h5{
  color: var(--white);
  margin-right: 1rem;
  opacity: 0.5;
  transition: 0.25s ease-in-out opacity, color ease-in 0.25s;
}
.sticky-navbar > div:nth-of-type(2) > a > h5:hover{
  opacity: 1;
  cursor: pointer;

}

.sticky-navbar {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  background-color: rgba(231,232,237,0);
  border-bottom: 0px solid var(--light-grey);
  transition: background-color 0.25s linear,
              border-bottom-width 0s step-end;
  z-index: 1000;
  padding: clamp(8px, 0.007vw, 13.6px) clamp(20px, 13.6vw, 262px);
  box-sizing: border-box;
  align-items: center;
}

.sticky-navbar.visible {
  background-color: rgba(231,232,237,1);
  border-bottom-width: 1px;
}
.sticky-navbar.visible > div:nth-of-type(2) > a > h5{
  color: var(--dark-grey);
}

.visible{
  opacity: 1;
  transition: opacity 0.25s linear;
}
.hidden{
  opacity: 0;
  transition: opacity 0.25s linear;
}

.big-box{
  position: relative;
  display: flex;
  flex-direction: column;
  width: calc(100%/3);
  aspect-ratio: 1 / 1;
  border-right: 1px solid var(--light-grey);
  border-bottom: 1px solid var(--light-grey);
  border-top-width: 0px;
  cursor: pointer;
  overflow: hidden;
}
.box{
  position: relative;
  display: flex;
  flex-direction: column;
  width: calc(100%/6);
  aspect-ratio: 1 / 1;
  border-right: 1px solid var(--light-grey);
  border-bottom: 1px solid var(--light-grey);
  border-top-width: 0px;
  cursor: pointer;
  overflow: hidden;
  box-sizing: border-box;

}
.box:first-child{
  border-left: 0px;
}

.box > .content{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
}
.icon{
  width: 2.5rem;
}
.icon-rd{
  width: 2rem;
  height: 2rem;
  aspect-ratio: 1 / 1;
  padding: 0.25rem;
  margin: 0.25rem 0.5rem 0.25rem 0;
  border-radius: 50%;
  box-sizing: border-box;

}
.icon-rd > img{
  filter: brightness(100);
  opacity: 0.7;
  width: 1.5rem;
}
.overlay > div > .icon{
  width: 1.5rem;
  height: auto;
  object-fit: contain;
}
.box > .content > .icon{
  align-self: center;
  margin: auto;
}
.box > .content > .container > .label{
  margin-left: 0.5rem;
}

.box:hover > .overlay{
  opacity: 1;
  transform: translateY(0%);
}
.box:hover > .overlay > .container > p, .box:hover > .overlay > div > .icon{
  opacity: 0.7;
  transform: translateY(0rem);
}

.box > .overlay{
  position: absolute;
  opacity: 0;
  background-color: var(--dark-blue);
  width: 100%;
  height: 100%;
  transform: translateY(25%);
  padding: 1rem 0.5rem 0.5rem 0.5rem;
  box-sizing: border-box;
  transition: all 0.25s ease-in-out;

}
.overlay > div > .icon{
  opacity: 0;
  transition: all 0.25s ease-in-out;
  transition-delay: 0.16s;
  transform: translateY(1rem);

  filter: var(--filter-color);
}
.overlay > .container > p{
  transition: all 0.25s ease-in-out;
  position: relative;
  transform: translateY(1rem);
  transition-delay: 0.24s;
  opacity: 0;
}
.container:nth-child(2) > .box{
  border-top: 1px solid var(--light-grey);
}

.article{
  border: 1px solid var(--light-grey);
  width: calc(100%/3);
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  border-right-width: 0px;
  border-bottom-width: 0px;
  height: 50vh;
  padding: 3rem 3.5rem;
  opacity: 1;
  transition: opacity 0.32s linear;
  overflow: hidden;
}

.article:hover{
  cursor: pointer;
  opacity: 0.5;
}
.article:hover > div > .hex-identicon{
  filter:saturate(100%);
}
.hex-identicon{
  transition: filter 0.25s linear;
  filter: saturate(0%)  ;
}

.identicon{
  border-radius: 50rem;
  background-color: var(--whiter);
  box-shadow: var(--box-shadow);
  margin-right: 0.25rem;
  padding: 0.125rem;
  height: fit-content;
}

#logo, .logo{
  width: 3.5rem;
}
#home-video{
  object-fit: cover;
  position: absolute;
  left:0;
  right:0;
  height: 100%;
  width: 100%;
  margin-left: auto;
  margin-right: auto;

  top: 0;
 
  z-index: -1;
}
.status{
  border: 1px solid var(--light-grey);
  padding: 0.5rem 1rem;
  border-radius: 10rem;
  color: var(--grey);
  box-sizing: border-box;
  align-items: center;
}
.footer{
  display: flex;

  width: 100%;
  box-sizing: border-box;
  padding-bottom: 10rem;
}

li{
  color: var(--grey);
  font-family: 'Geist';
  font-size: 1rem;
  list-style-type: none;
  text-transform: capitalize;
  margin-top: 1.5rem;
  font-weight: 500;
  display: flex;
  align-items: center;
}
li > img{
  height: 1rem;
  margin-right: 0.25rem;
  opacity: 0.5;
}
.invert{
    filter: invert(1);

}

#gov-bg{
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover;
}

.banner{
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover;

  height: 50vh;
}

.hidden-desktop{
  display: none;
}
.hidden-mobile{
  display: flex;
}
.to-col{
  flex-direction: row;
}
.hamburger{
  position: absolute;
  top: 0;
  left: 1rem;
  height: 3rem;
  width: 3rem;
  margin-top: 1rem;
}
.hamburger:hover{
  cursor: pointer;
}

.hamburger-menu{
  position: fixed;
  top: 0;
  z-index: 1001;
  background-color: var(--dark-blue);
  width: 100%;
  height: 100%;
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.25s ease-in;
}
.hamburger-menu.visible{
  display: flex;
  opacity: 1;
  pointer-events: all;
  transition: opacity 0.25s ease-in;
}

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}
.spinner{
  width: 1.5rem;
  margin-right: 0.5rem;
}
/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
.pill{
  display: flex;
  padding: 0.2rem 0.75rem;
  justify-content: center;
  align-items: center;
  border: 1px solid var(--light-grey);
  background-color: var(--white);
  position: absolute;
  border-radius: 100rem;
  bottom: 0;
  transition: all 0.25s linear;
}

@media (max-width: 768px) {
  /* Tablet and up styles here */
  .wrapper{
    padding: 5rem 0;
  }

  .container.half-width, .width-2, .width-3{
    width: 100%;
  }
  .col.align-self-end{
    margin-left: 0;
  }
  .to-col{
