  }
  #landing.padding{
    padding: 3rem;
  }
  .box{
    width: calc(100%/3);
  }
  .article{
    width: 50%;
  }
  .sticky-navbar{
    padding: 1rem 1rem;
  }
}


.animate-aurora{
  animation: aurora 8s ease-in-out infinite alternate;
  position: relative;

  letter-spacing: -0.075rem;
}
  

@keyframes aurora {
  0% {
    background-position: 0% 50%;
    transform: rotate(-5deg) scale(0.9);
  }
  25% {
    background-position: 50% 100%;
    transform: rotate(5deg) scale(1.1);
  }
  50% {
    background-position: 100% 50%;
    transform: rotate(-3deg) scale(0.95);
  }
  75% {
    background-position: 50% 0%;
    transform: rotate(3deg) scale(1.05);
  }
  100% {
    background-position: 0% 50%;
    transform: rotate(-5deg) scale(0.9);
  }
}


.marquee-container {
  display: flex;
  overflow: hidden;
  padding: 0.5rem;
  --duration: 26s;
  --gap: 1.5rem;
  gap: var(--gap);
  position: absolute;
}

.marquee-container.horizontal {
  flex-direction: row;
}

.marquee-container.vertical {
  flex-direction: column;
}

.marquee-track {
  display: flex;
  flex-shrink: 0;
  justify-content: space-around;
  gap: var(--gap);
  animation-play-state: running;
}

.marquee-track.horizontal {
  flex-direction: row;
  animation: marquee-horizontal var(--duration) linear infinite;
}

.marquee-track.vertical {
  flex-direction: column;
  animation: marquee-vertical var(--duration) linear infinite;
}

.marquee-track.reverse {
  animation-direction: reverse;
}

.marquee-container:hover .pause-on-hover {
  animation-play-state: paused;
}

/* Animations */
@keyframes marquee-horizontal {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-100%);
  }
}

@keyframes marquee-vertical {
  0% {
    transform: translateY(0%);
  }
  100% {
    transform: translateY(-100%);
  }
}

.flickering-grid-container {
  width: 100%;
  height: 100%;
  position: absolute;
  overflow: hidden;
  opacity: 0.5;
  transform: translateX(-4.5rem) translateY(-6.5rem);
}

.flickering-canvas {
  display: block;
  position: absolute;

}

